.menu_primary .menu__item {
  position: relative;
  color: #4b4b4b;
  transition: color 0.3s; }

.menu_primary .menu__item:nth-child(odd) {
  background: #f6f6f6; }

.menu_primary .menu__item:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 100%;
  background: #ff5555;
  transition: width 0.4s; }

.menu_primary .menu__item:after {
  content: '';
  display: block;
  border: 22.5px solid transparent;
  border-left: 12px solid #ff5555;
  position: absolute;
  left: 0;
  margin-left: -12px;
  top: 0;
  transition: all 0.4s; }

.menu_primary .menu__item:not(.active):hover:after {
  margin-left: 0; }

.menu_primary .menu__item.active {
  color: #fff; }
  .menu_primary .menu__item.active:after {
    left: 100%;
    margin-left: 0; }
  .menu_primary .menu__item.active:before {
    width: 100%; }

.menu_primary .menu__link {
  position: relative;
  width: 100%;
  height: 45px;
  line-height: 45px;
  padding: 0 25px;
  text-align: right;
  font-size: 20px;
  text-transform: uppercase;
  z-index: 10; }

