  display: block;
  position: absolute;
  right: 0;
  top: 0; }

.custom-input_number .custom-input__arrow {
  display: block;
  background: #f55;
  color: #fff;
  width: 16px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  cursor: pointer;
  font-size: 20px;
  overflow: hidden; }

.custom-input_number .custom-input__arrow:hover {
  background: #ff3131; }

.custom-input_number .custom-input__arrow:before {
  content: '';
